.PanelButton {
  background-color: rgb(5, 5, 5);
  padding: 12px;
  text-align: left;
  cursor: pointer;
}

.PanelButtonActive {
  background-color: rgb(143, 143, 143);
  padding: 12px;
  color: black;
  text-align: left;
  cursor: pointer;
}

.PanelButton:hover {
  background-color: rgb(61, 61, 61);
}

.LeftPanel {
  position: relative;
  left: 0;
  top: 0;
  padding-top: 70px;
  overflow: auto;
  background-color: black;
  width: 200px;
  float: left;
  height: 100%;
}

.settingTitle {
  position: relative;
  text-align: left;
  margin-left: 220px;
}

.SettingArea {
  position: fixed;
  right: 0;
  bottom: 0;
  top: 60px;
  left: 200px;
  text-align: left;
  padding: 12px;
  overflow: auto;
}

.UserIcon {
    margin: 5px;
    position: static;
    position: initial;
    width: 100px;
    height: 50px;
    color: white;
    cursor: pointer;
    padding: 10px;
    background-color: rgb(70, 70, 70);
}

.UserIconSELECTED {
    margin: 5px;
    position: static;
    position: initial;
    width: 100px;
    height: 50px;
    color: rgb(0, 0, 0);
    padding: 10px;
    background-color: rgb(255, 255, 255);
}

.UserIcon:hover {
    background-color: rgb(112, 111, 111);
}


.UserPNG {
    width: 30px;
    padding: 1px;
    background-color: rgb(255, 255, 255);
}
.UsersContainer {
    background-color: rgb(42, 42, 42);
    width: 100%;
    height: 400px;
    overflow-y: scroll;
}
.UserAction {
    width: 300px;
    height: 20px;
    margin: 4px;
    padding: 4px;
    color: white;
}

.UserAction:hover{
    color: rgb(17, 0, 255);
    cursor: pointer;
}

.UserActionlabel {
    float:  left;
    margin-right: 5px;
}
.App {
  position: fixed;
  font-family: sans-serif;
  text-align: center;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.settingTable {
  text-anchor: top;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  color: white;
  background-color: rgb(24, 23, 23);
}

